@font-face {
    font-family: quickLight;
    src: url(../../fonts/Quicksand-Light.ttf);
  }

.auth-form {
    margin: 0 auto 10px auto;
    width: 85vw;
    height: 320px;
    display: flex;
    flex-direction: column;
    color: white;
    font-family: quickLight;
    font-size: 30px;
    padding: 0;
    
}

.input {
    border-radius: 15px;
    border: solid 3px #466060;
    background-color: #FFFFFF50;
    height: 40px;
    margin-bottom: 0;
    margin-top: 5px;
}

.input:first-of-type {
    margin-bottom: 2vh;
}

.input:last-of-type {
    margin-bottom: 9vh;
}

.button {
    margin-top: -2vh;
}